@font-face {
  font-family: "Maruburi";
  src: url("/src/fonts/MaruBuri-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: "Maruburi", sans-serif;
}

body {
  font-family: "Maruburi", sans-serif;
  text-align: center;
  margin: 0;
  padding: 0;
  background: hsl(30, 30%, 90%);
}

.container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background-color: white;
}

.image-section {
  position: relative;
  text-align: center;
}

.image-section img {
  width: 100%;
  border-radius: 10px;
}

.overlay-text {
  position: absolute;
  width: 100%;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* background: rgba(0, 0, 0, 0.5); */
  /* color: white; */
  /* padding: 10px 20px; */
  /* border-radius: 5px; */
  font-size: 24px;
  font-weight: bold;

  color: black;
}

.intro table {
  width: fit-content;
  margin: 0 auto;
  border-collapse: collapse;
}

.intro td {
  vertical-align: bottom;
}
.intro td:first-child {
  font-size: 20px;
  font-weight: bold;
}
.intro td:nth-child(3) {
  font-size: 20px;
  padding: 0 0.5em;
}
.intro td:last-child {
  font-size: 20px;
  font-weight: bold;
}

.countdown {
  font-size: 20px;
  margin: 20px 0;
  font-weight: bold;
}

.info {
  margin-top: 2em;
  border-top: 1px solid rgb(191, 191, 191);
}

.info > h2 {
  margin-bottom: 5px;
  text-decoration: hsl(30, 30%, 50%) 2px double underline;
  text-underline-position: under;
}

.account {
  margin-top: 20px;
}

.account h2 {
  margin-bottom: 10px;
}

button {
  background: hsl(30, 30%, 50%);
  color: white;
  border: none;
  margin: 5px;
  padding: 5px;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background: hsl(00, 30%, 50%);
}

.accTable {
  width: 100%;
  border-collapse: collapse;
  border-top: 3px double hsl(30, 30%, 50%);
  border-bottom: 3px double hsl(30, 30%, 50%);
}

.accTable thead {
  border-bottom: 1px solid hsl(30, 30%, 50%);
}

.accTable th {
  white-space: nowrap;
}
